import React, { ButtonHTMLAttributes, useRef } from "react";
import { Draggable, Droppable } from "react-beautiful-dnd";
import styled from "styled-components";
import { DraggableCard } from "./DragabbleCard";
import {useForm} from "react-hook-form"
import { BoardState, ITodo, toDoState } from "../atoms";
import { useSetRecoilState } from "recoil";

const Wrapper = styled.div`
  width:300px;
  padding-top: 10px ;
  background-color: ${(props) => props.theme.boardColor};
  border-radius: 5px;
  min-height: 300px;
  display:flex ;
  flex-direction: column ;
  position: relative ;
button{
position: absolute;
right:0px;
margin-right:10px ;
}
`;
const Title = styled.h2`
 text-align: center;
 font-weight: 600;
 margin-bottom:10px;
 font-size: 18px;
`
const Area = styled.div<IArea>`
background-color: ${props => props.isDraggingOver ? "#dfe6e9":props.draggingFromThis ?"#b3bec3":"transpatent"};
flex-grow:1;
padding:20px;
`;
const Form = styled.form`
   
  input{
    width:100%;
   
  }
`;

interface IBoardProps {
    toDos: ITodo[];
    boardId: string;
    index:number;
}
interface IArea {
  isDraggingOver:boolean,
  draggingFromThis:boolean,
}

interface IForm {
  toDo: string;
}

function Board({toDos, boardId ,index}:IBoardProps){
  const setToDos = useSetRecoilState(toDoState)
  const setBoards = useSetRecoilState(BoardState)
  const {register, setValue, handleSubmit} = useForm<IForm>();
  const onValid = ({toDo}:IForm) => {
    const newToDo = {
      id:Date.now(),
      text: toDo, //input  
    }
    setToDos(allBoards => {
      return {
        ...allBoards,
        [boardId] : [...allBoards[boardId],newToDo]
        // "doing" : [...allBoards["doing"],newToDo]
       
      };
    });
    setValue("toDo", "");
  }


  function handleClick() {

    // setToDos(allBoards => {
    //   const Dt = {...allBoards}
    //   delete Dt[boardId]
    
    //   return {
    //     ...Dt
    //   };
    // });
    //    setToDos  
    setBoards(Board => {
      const Dt = [...Board]
      Dt.splice(index, 1)
      console.log(Dt)
    
      return [
        ...Dt
      ]
    })

   
  }
 
 
 
 
    return(
      <Draggable draggableId={boardId} index={index} key={boardId}>
      {(magic) => (
        <Wrapper
        {...magic.dragHandleProps}
        {...magic.draggableProps}
        ref={magic.innerRef}>

        <Title>{boardId}</Title>
        <button onClick={handleClick}>x</button>
        <Form onSubmit={handleSubmit(onValid)}>
          <input {...register("toDo",{required: true})} type="text" placeholder={`Add task on ${boardId}`}/>
           {/* inputRef   input  .*/}
           
        </Form>
       
        <Droppable droppableId={boardId}>
        
            {(magic, info) => (
              
              <Area 
              isDraggingOver={info.isDraggingOver} 
              //  Draggable  Droppable     
              // Droppable    Draggable ID
              draggingFromThis={Boolean(info.draggingFromThisWith)}
                                //string   true
              ref={magic.innerRef} 
              //ref  HTML    
              {...magic.droppableProps}

              >

                {toDos.map((toDo, index) => (
                <DraggableCard 
                key={toDo.id} 
                toDoId={toDo.id} 
                index={index}
                toDoText={toDo.text}/>
                ))}
                
                {magic.placeholder}
              </Area>
            )}


            
          </Droppable>
          </Wrapper>
      )}
          </Draggable>
    )
}

export default React.memo(Board);